body {
  position: relative;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 15px;
  line-height: 22px;
  font-weight: normal;
  color: #000000;
  background: #ffffff; }

li {
  list-style: none; }

a {
  text-decoration: none; }

.visual-hidden {
  clip: rect(0 0 0 0);
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px; }

.button--scroll {
  text-align: center;
  font-size: 16px;
  font-weight: 500;
  line-height: 45px;
  color: #000000;
  background-color: #00ffd8; }
  .button--scroll:hover {
    box-shadow: 0px 2px 4.75px 0.25px #00ffd8; }

.button--scroll__services {
  box-sizing: border-box;
  font-weight: 900;
  line-height: 60px;
  width: 280px;
  min-height: 60px;
  margin: 0 auto;
  margin-bottom: 10px;
  text-transform: uppercase; }

.button--scroll__contacts {
  box-sizing: border-box;
  font-weight: 900;
  line-height: 58px;
  width: 280px;
  min-height: 58px;
  margin: 0 auto;
  margin-bottom: 85px;
  background-color: inherit;
  color: #ffffff;
  border: solid 1px #00ffd8;
  text-transform: uppercase; }

/*-- Tablet --*/
/*-- Desktop --*/
.title {
  font-family: "Raleway", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 900;
  color: #ffffff;
  text-align: center; }

/*-- Tablet --*/
@media (min-width: 768px) {
  .title {
    font-size: 72px;
    font-weight: 900; } }

/*-- Desktop --*/
.container {
  box-sizing: border-box;
  width: 320px;
  margin: 0 auto; }

/*-- Tablet --*/
@media (min-width: 768px) {
  .container {
    padding-left: 22px;
    padding-right: 22px;
    width: 768px; } }

/*-- Desktop --*/
@media (min-width: 1200px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
    width: 1200px; } }

.page-header {
  background-color: #fbfbfb;
  width: 100%;
  margin: 0 auto; }

.main-nav {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  width: 320px;
  min-height: 50px;
  margin: 0 auto;
  align-items: flex-start;
  justify-content: space-between; }

.main-nav__logo {
  padding-top: 6px;
  padding-left: 20px; }

.main-nav__toggle-button {
  border: none;
  cursor: pointer;
  background: none;
  line-height: 0;
  font-size: 0;
  padding: 0;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 17px;
  padding-bottom: 17px;
  width: 18px;
  min-height: 16px; }

.main-nav__logo-image {
  display: block;
  margin: 0 auto; }

.main-nav__list {
  display: none;
  margin: 0;
  padding: 0;
  position: absolute;
  bottom: -160px;
  width: 100%;
  z-index: 10; }
  .main-nav__toggle:checked ~ .main-nav__list {
    display: block; }

.main-nav__link {
  display: block;
  font-size: 12px;
  text-transform: uppercase;
  line-height: 36px;
  min-height: 36px;
  padding-top: 4px;
  text-align: center;
  color: #383838;
  background-color: #e7e7e7;
  box-shadow: inset 0px 1px 2px 0.02px #dddddd; }
  .main-nav__item:nth-child(2) > .main-nav__link {
    box-shadow: none; }

.main-nav__link--desktop {
  display: none; }

.main-nav__phone {
  display: none; }

.main-nav__feedback {
  display: none; }

/*-- Tablet --*/
@media (min-width: 768px) {
  .main-nav {
    width: 640px;
    min-height: 52px;
    padding-left: 64px;
    padding-right: 64px;
    justify-content: flex-start; }
  .main-nav__logo {
    padding-top: 8px;
    padding-left: 0; }
  .main-nav__toggle-button {
    width: 26px;
    min-height: 21px;
    padding-bottom: 14px; }
  .main-nav__toggle-button--top {
    margin-left: 19px; }
  .main-nav__list {
    left: 0;
    width: 207px; }
  .main-nav__phone {
    display: block;
    line-height: 20px;
    font-size: 14px;
    font-weight: bold;
    word-spacing: -2px; }
  .main-nav__phone--top {
    margin-left: auto;
    margin-top: 19px;
    margin-bottom: 13px; } }

/*-- Desktop --*/
@media (min-width: 1200px) {
  .main-nav {
    width: 1170px;
    min-height: 100px;
    padding-left: 15px;
    padding-right: 15px; }
  .main-nav__logo-image {
    width: 141px;
    height: 60px; }
  .main-nav__logo-head {
    padding: 0;
    margin-top: 20px;
    margin-bottom: 20px;
    margin-right: 58px; }
  .main-nav__toggle-button {
    display: none; }
  .main-nav__list {
    display: flex;
    position: static;
    width: auto; }
    .main-nav__toggle:checked ~ .main-nav__list {
      display: flex; }
  .main-nav__link--desktop {
    display: block; }
  .main-nav__link {
    font-weight: 900;
    font-size: 10px;
    line-height: 20px;
    text-align: center;
    color: #707070;
    background-color: transparent;
    border: none;
    min-height: inherit;
    padding: 0;
    margin-right: 25px;
    margin-top: 40px;
    margin-bottom: 40px;
    box-shadow: none; }
    .main-nav__link:hover {
      color: #000000; }
  .main-nav__phone {
    font-size: 18px;
    font-weight: bold;
    margin-top: auto;
    margin-bottom: auto;
    word-spacing: -4px; }
  .main-nav__feedback {
    display: block;
    width: 190px;
    min-height: 45px;
    margin-left: 25px;
    margin-top: 26px;
    margin-bottom: 30px; } }

.toggle__line {
  display: block;
  margin-bottom: 4px;
  height: 2px;
  width: 16px;
  background-color: #959595;
  border-left: solid;
  border-right: solid;
  border-left-width: 1px;
  border-right-width: 1px;
  border-color: #adadad; }
  .toggle__line:last-child {
    margin-bottom: 0; }

/*-- Tablet --*/
@media (min-width: 768px) {
  .toggle__line {
    width: 24px;
    margin-bottom: 7px; } }

.main-banner {
  position: relative;
  display: flex;
  flex-direction: column; }

.main-banner__title {
  width: 260px;
  letter-spacing: 0.05em; }

.main-banner__title--main {
  margin: 0 auto;
  margin-bottom: 20px;
  margin-top: 128px;
  text-transform: uppercase;
  line-height: 19px; }

.main-banner__subtitle {
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: normal;
  width: 260px;
  line-height: normal; }

.main-banner__subtitle--main {
  margin: 0 auto;
  margin-bottom: 60px; }

.main-banner__year {
  font-weight: normal;
  color: #00ffd8; }

.main-banner__year--built {
  color: #000000; }

.main-banner__background {
  position: absolute;
  width: 100%;
  height: 465px;
  background-repeat: no-repeat;
  background-color: #000000;
  z-index: -1; }

.main-banner__text {
  font-size: 12px;
  line-height: 18px;
  width: 274px;
  margin: 0 auto; }
